.blog-post-full {
    max-width: 800px;
    margin: 0 auto;
    padding: 40px 20px;
    background-color: var(--background-color);
    color: var(--text-color);
  }
  
  .blog-post-full h1 {
    font-size: 2.5rem;
    color: var(--primary-color);
    margin-bottom: 10px;
  }
  
  .post-date {
    font-size: 0.9rem;
    color: var(--text-muted);
    margin-bottom: 20px;
  }
  
  .post-content {
    font-size: 1.1rem;
    line-height: 1.6;
  }
  
  .post-content p {
    margin-bottom: 20px;
  }
  
  .post-content h2 {
    font-size: 1.8rem;
    color: var(--secondary-color);
    margin-top: 30px;
    margin-bottom: 15px;
  }
  
  .post-content ul, .post-content ol {
    margin-bottom: 20px;
    padding-left: 20px;
  }
  
  .post-content img {
    max-width: 100%;
    height: auto;
    margin: 20px 0;
    border-radius: 8px;
  }
  
  @media (max-width: 768px) {
    .blog-post-full h1 {
      font-size: 2rem;
    }
  
    .post-content {
      font-size: 1rem;
    }
  }